import React from 'react';
import Paper from '@material-ui/core/Paper';
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import { Grid, Table, TableHeaderRow, TableFilterRow } from '@devexpress/dx-react-grid-material-ui';
import {
    FilteringState,
    IntegratedFiltering,

} from '@devexpress/dx-react-grid';
import { TableRow, TableCell } from '@material-ui/core';
import { generateRows } from './demo-data/generator';
import FilterValuesProvider from './demo-data/filter-values-provider'

const columns = [
    { name: 'name', title: 'Name' },
    { name: 'gender', title: 'Gender' },
    { name: 'city', title: 'City' },
    { name: 'car', title: 'Car' },
];

const FilterCell = ({
    columnFilterValues,
    column,
    children,
    filter,
    onFilter,
    ...restProps
}) => {
    console.log(column)
    const filterValues = ["", ...columnFilterValues[column.name]];
    const filterValue = filter && filter.value;
    const onChange = e =>
        onFilter({ columnName: column.name, value: e.target.value });
    return (
        <TableFilterRow.Cell {...restProps} column={column}>
            <Select value={filterValue} onChange={onChange}>
                {filterValues.map(value => (
                    <MenuItem value={value || null}>{value}</MenuItem>
                ))}
            </Select>
        </TableFilterRow.Cell>
    );
};

const predicate = (value, filter) => value === filter.value;
const integratedFilteringColumnExtensions = columns.map(c => ({
    columnName: c.name,
    predicate
}));

class DemoTable extends React.PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            columns: [
                { name: 'name', title: 'Name' },
                { name: 'gender', title: 'Gender' },
                { name: 'city', title: 'City' },
                { name: 'car', title: 'Car' },
            ],
            rows: generateRows({ length: 8 }),
            filters: [{ columnName: "city", value: "Paris" }]
        };
        this.changeFilters = filters =>
            this.setState({ filters: filters.filter(f => f.value) });
    }

    render() {
        const { rows, columns, filters } = this.state;
        return (
            <Paper>
                <Grid
                    rows={rows}
                    columns={columns}
                >
                    <FilteringState defaultFilters={[]}
                        filters={filters}
                        onFiltersChange={this.changeFilters} />
                    <IntegratedFiltering columnExtensions={integratedFilteringColumnExtensions} />
                    <Table />
                    <TableHeaderRow />
                    <TableFilterRow cellComponent={FilterCell} />
                    <FilterValuesProvider />
                </Grid>
            </Paper>
        );
    }
};
export default DemoTable