import React from "react";
import {
    Plugin,
    Template,
    TemplateConnector,
    TemplatePlaceholder
} from "@devexpress/dx-react-core";
import { TableFilterRow, Table } from "@devexpress/dx-react-grid";

const isFilterTableCell = (tableRow, tableColumn) =>
    tableRow.type === TableFilterRow.ROW_TYPE &&
    tableColumn.type === Table.COLUMN_TYPE;

const FilterValuesProvider = React.memo(() => (
    <Plugin name="FilterValuesProvider">
        <Template
            name="tableCell"
            predicate={({ tableRow, tableColumn }) =>
                isFilterTableCell(tableRow, tableColumn)
            }
        >
            {params => (
                <TemplateConnector>
                    {({ columnFilterValues }) => (
                        <TemplatePlaceholder params={{ ...params, columnFilterValues }} />
                    )}
                </TemplateConnector>
            )}
        </Template>
    </Plugin>
));
export default FilterValuesProvider